@import url('https://fonts.googleapis.com/css?family=Arvo|Maitree');

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
}

html, body {
  height: 100%;
  font-size: 12px;
  font-family: 'Maitree', serif;
  color: black;
  font-weight: 400;
}

/* Desktops and laptops ----------- */
@media only screen
and (min-width : 600px) {
  body {
    font-size: 15px;
  }
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-right: 5%;
  margin-left: 5%;
  min-height: 100vh;
}



